<template>
  <el-table :data="lists" @selection-change="$emit('getMultipleSelection', $event)">
    <el-table-column type="selection" width="60"></el-table-column>
    <el-table-column label="" width="600">
      <template #default="scope">
        <span v-if="scope.row.file_type !== 'file'" style="cursor: pointer;" v-html="scope.row.filename"
              @click="$emit('getFileLists', scope.row)"></span>
        <span @click="$emit('getFiles',scope.row)" style="cursor: pointer;" v-else v-html="scope.row.filename"></span>
      </template>
    </el-table-column>
    <el-table-column label="" width="100">
      <template #default="scope">
        <span v-html="scope.row.file_type.replace(/^\S/, s => s.toUpperCase())"></span>
      </template>
    </el-table-column>
    <el-table-column label="" prop="auth" width="100"></el-table-column>
    <el-table-column label="" prop="size" width="100"></el-table-column>
    <el-table-column label="" prop="time" width="150"></el-table-column>
    <el-table-column label="" align="right">
      <template #default="scope">
        <el-button size="mini" type="text"
                   v-if="scope.row.file_type !== 'file' && Permission.auth.indexOf(fileURL.save) > -1"
                   @click="$emit('addFile',scope.row)">
        </el-button>
        <el-button size="mini" type="text"
                   v-if="scope.row.file_type === 'file' && Permission.auth.indexOf(fileURL.read) > -1"
                   @click="$emit('getFiles',scope.row)">
        </el-button>
        <el-button size="mini" type="text" v-if="Permission.auth.indexOf(fileURL.rename) > -1"
                   @click="$emit('renameFile',scope.row)">
        </el-button>
        <el-button size="mini" type="text" v-if="Permission.auth.indexOf(fileURL.chmod) > -1"
                   @click="$emit('chmodFile',scope.row)">
        </el-button>
        <el-button size="mini" type="text" v-if="Permission.auth.indexOf(fileURL.zip) > -1"
                   @click="$emit('composeFile',scope.row)">
        </el-button>
        <el-button size="mini" type="text"
                   v-if="scope.row.file_type === 'file' && scope.row.filename.split('.')[1] === 'zip' && Permission.auth.indexOf(fileURL.unzip) > -1"
                   @click="$emit('unComposeFile',scope.row)">
        </el-button>
        <el-button size="mini" type="text" v-if="Permission.auth.indexOf(fileURL.download) > -1"
                   @click="$emit('downloadFile',scope.row)">
        </el-button>
        <el-button size="mini" type="text" v-if="Permission.auth.indexOf(fileURL.upload) > -1"
                   @click="$emit('uploadFile',scope.row)">
        </el-button>
        <el-button size="mini" type="text" v-if="Permission.auth.indexOf(fileURL.delete) > -1"
                   @click="$emit('deleteFile',scope.row)">
        </el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'FileLists',
  props: ['lists', 'fileURL']
};
</script>

