<template>
  <div id="dialog">
    <el-dialog v-model="visible.detail" :title="tabModel.label || ''" center width="1550px" :show-close="false"
               :close-on-click-modal="false" :close-on-press-escape="false" :fullscreen="fullscreen">
      <el-row :gutter="24">
        <el-col :span="24" style="margin-top: -65px;color: white;">
          <i class="el-icon-full-screen" style="font-size: 25px" @click="fullscreen = !fullscreen"></i>
        </el-col>
        <!---->
        <el-col :span="6" style="margin-bottom: 20px">
          <el-input placeholder="" v-model="filterText" clearable style="margin-bottom: 20px"
                    suffix-icon="el-icon-search"></el-input>
          <el-tree :data="details"
                   :highlight-current="true"
                   :props="treeProps"
                   accordion
                   node-key="md5"
                   :filter-node-method="filterNode"
                   @node-click="getFileContent"
                   ref="tree"
                   :default-expand-all="false"
                   style="background-color: #272822;line-height: 20px">
          </el-tree>
        </el-col>
        <!---->
        <!---->
        <el-col :span="18" class="details">
          <el-form :model="form.model" ref="fileSave">
            <el-form-item>
              <el-tabs type="border-card" lazy closable v-model="tabModel.name" @tab-click="goto"
                       @tab-remove="removeTab" style="text-align: left!important;">
                <el-tab-pane v-for="item in fileTabs" :label="item.label" :key="item.name"
                             :name="item.name"></el-tab-pane>
                <el-card shadow="always">
                  <codemirror @change="updateContent" ref="edit" :value="tabModel.content" :options="options"
                              style="line-height: 20px"></codemirror>
                </el-card>
              </el-tabs>
            </el-form-item>
            <SubmitButton v-if="Permission.auth.indexOf(savePermission) > -1" :form="form" re-form="fileSave"
                          @closeDialog="$emit('closeDialog')"></SubmitButton>
            <el-main style="text-align: center" v-else>
              <el-button type="default" size="medium" @click="$emit('closeDialog')" plain></el-button>
            </el-main>
          </el-form>
        </el-col>
        <!---->
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import { codemirror } from 'vue-codemirror-lite';
import SubmitButton from '@/components/common/SubmitForm';
import URLS from '@/api/urls';

/*  */
require('codemirror/mode/php/php.js');
require('codemirror/mode/sql/sql.js');
require('codemirror/addon/hint/sql-hint.js');
require('codemirror/addon/hint/show-hint.js');
require('codemirror/mode/markdown/markdown.js');
require('codemirror/mode/xml/xml.js');
require('codemirror/addon/hint/javascript-hint.js');
require('codemirror/mode/javascript/javascript.js');
require('codemirror/addon/selection/active-line');
/*  */
require('codemirror/theme/monokai.css');
/*  */
require('codemirror/addon/fold/foldgutter.css');
require('codemirror/addon/fold/foldcode.js');
require('codemirror/addon/fold/foldgutter.js');
require('codemirror/addon/fold/brace-fold.js');
require('codemirror/addon/fold/brace-fold.js');
require('codemirror/addon/fold/comment-fold.js');
/*  */
require('codemirror/addon/edit/matchbrackets.js');

export default {
  name: 'FileDetails',
  props: ['details', 'detailVisible'],
  components: { SubmitButton, codemirror },
  data() {
    return {
      visible: { detail: this.detailVisible },
      treeProps: { label: 'filename', children: 'children', isLeaf: false },
      fullscreen: false,
      filterText: '',
      form: { model: { content: '', path: '' }, url: URLS.file.update, $refs: '' },
      /*  */
      options: {
        /*  */
        mode: 'text/x-php',
        /*  */
        tabSize: 4,
        /*  */
        lineNumbers: true,
        /* theme */
        theme: 'monokai',
        /*  ctrl-space */
        extraKeys: { 'Ctrl-Space': 'autocomplete' },
        /*  */
        lineWrapping: false,
        /*  */
        foldGutter: true,
        // gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        /*  */
        indentWithTabs: true,
        /*  */
        matchBrackets: true,
        /*  */
        cursorHeight: 1,
        /*  */
        autoRefresh: true,
        /*  */
        styleActiveLine: true,
        /*  */
        readOnly: false
      },
      mode: {
        markdown: 'text/markdown',
        php: 'text/x-php',
        xml: 'text/xml',
        json: 'application/ld+json',
        sql: 'text/x-mysql'
      },
      savePermission: URLS.file.update
    };
  },
  watch: {
    detailVisible() {
      this.visible.detail = this.detailVisible;
      /*  */
      if (this.visible.detail) {
        this.options.readOnly = this.Permission.auth.indexOf(this.savePermission) < 0;
      }
    },
    filterText(val) {
      this.$refs.tree.filter(val);
    },
    tabModel() {
      this.form.model.content = this.tabModel.content;
    }
  },
  computed: {
    fileTabs() {
      return this.$store.state.file.tabs;
    },
    tabModel() {
      return { ...this.$store.state.file.tabModel };
    }
  },
  methods: {
    /**
     * todo:
     * @param file
     */
    async getFileContent(file) {
      if (file.file_type !== 'file') {
        return false;
      }
      let ext = (file.filename.split('.') || [])[1] || 'php';
      /*  */
      let imgExt = ['png', 'jpg', 'jpeg', 'gif', 'mp4', 'flv'];
      if (imgExt.includes(ext.toLowerCase())) {
        this.$message.error('!');
        return false;
      }
      this.form.$refs = this.$refs;
      this.setOptionsMode(ext);
      this.form.model.path = file.path;
      let tabs = { name: file.md5, label: file.filename, path: file.path };
      await this.$store.dispatch('file/getFileContent', tabs);
    },
    /**
     * TODO:mode
     * @param ext
     */
    setOptionsMode: function (ext) {
      switch (ext.toLowerCase()) {
        case 'xml':
          this.options.mode = this.mode.xml;
          break;
        case 'md':
          this.options.mode = this.mode.markdown;
          break;
        case 'json':
        case 'lock':
          this.options.mode = this.mode.json;
          break;
        case 'sql':
          this.options.mode = this.mode.sql;
          break;
        default:
          this.options.mode = this.mode.php;
          break;
      }
    },
    /**
     * TODO
     * @param value
     * @param data
     */
    filterNode: function (value, data) {
      return value ? data.filename.toLowerCase().indexOf(value.toLowerCase()) !== -1 : true;
    },
    /**
     * todo:
     * @param tab
     * @return {Promise<void>}
     */
    async goto(tab) {
      await this.fileTabs.forEach(item => {
        if (tab.props.label === item.label) {
          this.form.$refs = this.$refs;
          this.form.model.path = item.path;
          this.$store.commit('file/UPDATE_MUTATIONS', { tabModel: item });
        }
      });
    },
    /**
     * todo:tab
     * @param tabName
     * @return {Promise<void>}
     */
    async removeTab(tabName) {
      await this.fileTabs.forEach((item, index) => {
        if (tabName === item.name) {
          this.form.$refs = this.$refs;
          this.form.model.path = item.path;
          let nextTab = this.fileTabs[index + 1] || this.fileTabs[index - 1];
          if (nextTab) {
            this.$store.dispatch('file/deleteTabs', { index: index, nextTab: nextTab });
          }
        }
      });
    },
    /**
     * todo:
     * @param content
     */
    updateContent(content) {
      this.form.$refs = this.$refs;
      this.form.model.content = content;
    }
  }
};
</script>

<style lang="less">
#dialog {
  .el-tree {
    min-height: 500px;
  }

  .CodeMirror {
    border: 1px solid #eee;
    min-height: 550px;
    height: auto !important;
    z-index: 1;
  }

  .CodeMirror-scroll {
    height: auto;
    min-height: 550px;
    max-height: 1000px;
    overflow-y: hidden;
    overflow-x: auto;
  }

  .CodeMirror-hscrollbar {
    left: 30px !important;
    height: 5px !important;
  }

  .details {
    .el-form-item__content {
      .el-tabs--border-card {
        width: 1120px !important;
      }
    }
  }
}
</style>
